/**
 * Upstream Proxy
 * 
 * Forwards requests to customer upstream URLs after payment verification
 */

export interface ProxyOptions {
  upstreamUrl: string;
  method: string;
  headers: Headers;
  body?: string | null;
  timeout?: number;
}

export interface ProxyResult {
  success: boolean;
  statusCode: number;
  headers: Record<string, string>;
  body: string;
  responseTime: number;
  error?: string;
}

/**
 * Proxy a request to the upstream URL
 */
export async function proxyRequest(options: ProxyOptions): Promise<ProxyResult> {
  const startTime = Date.now();
  const timeout = options.timeout || 30000; // 30 second default timeout

  try {
    // Prepare headers (exclude host and connection headers)
    const proxyHeaders: Record<string, string> = {};
    options.headers.forEach((value, key) => {
      const lowerKey = key.toLowerCase();
      // Skip headers that shouldn't be forwarded
      if (
        lowerKey !== 'host' &&
        lowerKey !== 'connection' &&
        lowerKey !== 'x-payment' && // Don't forward payment header to upstream
        lowerKey !== 'x-forwarded-host'
      ) {
        proxyHeaders[key] = value;
      }
    });

    // Add forwarded headers
    proxyHeaders['X-Forwarded-For'] = options.headers.get('x-forwarded-for') || 'unknown';
    proxyHeaders['X-Forwarded-Proto'] = options.headers.get('x-forwarded-proto') || 'https';

    // Create fetch request with timeout
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), timeout);

    try {
      const response = await fetch(options.upstreamUrl, {
        method: options.method,
        headers: proxyHeaders,
        body: options.body || undefined,
        signal: controller.signal,
      });

      clearTimeout(timeoutId);

      // Read response body
      const body = await response.text();

      // Convert response headers to object
      const responseHeaders: Record<string, string> = {};
      response.headers.forEach((value, key) => {
        responseHeaders[key] = value;
      });

      const responseTime = Date.now() - startTime;

      return {
        success: response.ok,
        statusCode: response.status,
        headers: responseHeaders,
        body,
        responseTime,
      };
    } catch (fetchError) {
      clearTimeout(timeoutId);
      throw fetchError;
    }
  } catch (error) {
    const responseTime = Date.now() - startTime;

    if (error instanceof Error && error.name === 'AbortError') {
      return {
        success: false,
        statusCode: 504,
        headers: {},
        body: JSON.stringify({ error: 'Upstream timeout' }),
        responseTime,
        error: 'Request timeout',
      };
    }

    return {
      success: false,
      statusCode: 502,
      headers: {},
      body: JSON.stringify({
        error: 'Upstream error',
        details: error instanceof Error ? error.message : 'Unknown error',
      }),
      responseTime,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

