// =============================================================================
// SESSION CACHE
// =============================================================================
// In-memory and persistent cache for session state management
// Can be extended to use Redis or other distributed cache

import { createLogger } from '@/lib/logger';
import type { WalletSession } from './wallet-session';
import type { ActiveSubscription } from './subscription-manager';

const logger = createLogger({ component: 'SessionCache' });

/**
 * Cache Configuration
 */
export interface CacheConfig {
  ttl?: number; // Time to live in seconds (default: 3600 = 1 hour)
  maxSize?: number; // Maximum number of entries (default: 10000)
}

/**
 * Session Cache
 * 
 * Provides caching layer for sessions and subscriptions.
 * Currently in-memory, can be extended to use Redis or other distributed cache.
 */
export class SessionCache {
  private sessions: Map<string, { data: WalletSession; expires: number }> = new Map();
  private subscriptions: Map<string, { data: ActiveSubscription; expires: number }> = new Map();
  private config: Required<CacheConfig>;

  constructor(config: CacheConfig = {}) {
    this.config = {
      ttl: config.ttl || 3600, // 1 hour default
      maxSize: config.maxSize || 10000,
    };

    // Clean up expired entries every minute
    setInterval(() => {
      this.cleanup();
    }, 60 * 1000);
  }

  /**
   * Store a session in cache
   */
  setSession(session: WalletSession): void {
    // Check cache size
    if (this.sessions.size >= this.config.maxSize) {
      this.evictOldestSession();
    }

    const expires = Date.now() + this.config.ttl * 1000;
    this.sessions.set(session.id, { data: session, expires });

    logger.debug({ sessionId: session.id }, 'Session cached');
  }

  /**
   * Get a session from cache
   */
  getSession(sessionId: string): WalletSession | null {
    const entry = this.sessions.get(sessionId);

    if (!entry) {
      return null;
    }

    // Check if expired
    if (Date.now() > entry.expires) {
      this.sessions.delete(sessionId);
      return null;
    }

    return entry.data;
  }

  /**
   * Remove a session from cache
   */
  deleteSession(sessionId: string): void {
    this.sessions.delete(sessionId);
    logger.debug({ sessionId }, 'Session removed from cache');
  }

  /**
   * Store a subscription in cache
   */
  setSubscription(subscription: ActiveSubscription): void {
    // Check cache size
    if (this.subscriptions.size >= this.config.maxSize) {
      this.evictOldestSubscription();
    }

    const expires = Date.now() + this.config.ttl * 1000;
    this.subscriptions.set(subscription.id, { data: subscription, expires });

    logger.debug({ subscriptionId: subscription.id }, 'Subscription cached');
  }

  /**
   * Get a subscription from cache
   */
  getSubscription(subscriptionId: string): ActiveSubscription | null {
    const entry = this.subscriptions.get(subscriptionId);

    if (!entry) {
      return null;
    }

    // Check if expired
    if (Date.now() > entry.expires) {
      this.subscriptions.delete(subscriptionId);
      return null;
    }

    return entry.data;
  }

  /**
   * Remove a subscription from cache
   */
  deleteSubscription(subscriptionId: string): void {
    this.subscriptions.delete(subscriptionId);
    logger.debug({ subscriptionId }, 'Subscription removed from cache');
  }

  /**
   * Clear all cached sessions
   */
  clearSessions(): void {
    this.sessions.clear();
    logger.info('All sessions cleared from cache');
  }

  /**
   * Clear all cached subscriptions
   */
  clearSubscriptions(): void {
    this.subscriptions.clear();
    logger.info('All subscriptions cleared from cache');
  }

  /**
   * Get cache statistics
   */
  getStats(): {
    sessions: number;
    subscriptions: number;
    maxSize: number;
  } {
    return {
      sessions: this.sessions.size,
      subscriptions: this.subscriptions.size,
      maxSize: this.config.maxSize,
    };
  }

  /**
   * Clean up expired entries
   */
  private cleanup(): void {
    const now = Date.now();
    let cleanedSessions = 0;
    let cleanedSubscriptions = 0;

    // Clean expired sessions
    for (const [sessionId, entry] of this.sessions.entries()) {
      if (now > entry.expires) {
        this.sessions.delete(sessionId);
        cleanedSessions++;
      }
    }

    // Clean expired subscriptions
    for (const [subscriptionId, entry] of this.subscriptions.entries()) {
      if (now > entry.expires) {
        this.subscriptions.delete(subscriptionId);
        cleanedSubscriptions++;
      }
    }

    if (cleanedSessions > 0 || cleanedSubscriptions > 0) {
      logger.debug(
        { cleanedSessions, cleanedSubscriptions },
        'Cache cleanup completed'
      );
    }
  }

  /**
   * Evict oldest session (LRU-like)
   */
  private evictOldestSession(): void {
    let oldestId: string | null = null;
    let oldestExpires = Infinity;

    for (const [sessionId, entry] of this.sessions.entries()) {
      if (entry.expires < oldestExpires) {
        oldestExpires = entry.expires;
        oldestId = sessionId;
      }
    }

    if (oldestId) {
      this.sessions.delete(oldestId);
      logger.debug({ sessionId: oldestId }, 'Evicted oldest session from cache');
    }
  }

  /**
   * Evict oldest subscription (LRU-like)
   */
  private evictOldestSubscription(): void {
    let oldestId: string | null = null;
    let oldestExpires = Infinity;

    for (const [subscriptionId, entry] of this.subscriptions.entries()) {
      if (entry.expires < oldestExpires) {
        oldestExpires = entry.expires;
        oldestId = subscriptionId;
      }
    }

    if (oldestId) {
      this.subscriptions.delete(oldestId);
      logger.debug({ subscriptionId: oldestId }, 'Evicted oldest subscription from cache');
    }
  }
}

// Singleton instance
let sessionCache: SessionCache | null = null;

/**
 * Get session cache instance
 */
export function getSessionCache(): SessionCache {
  if (!sessionCache) {
    sessionCache = new SessionCache();
  }

  return sessionCache;
}

