// =============================================================================
// Enhanced Upstream Proxy with Retry Logic and Circuit Breaker
// =============================================================================
// Production-grade proxy with:
// - Exponential backoff retry
// - Circuit breaker pattern
// - Connection pooling
// - Better error handling

import { createLogger } from './logger';

const logger = createLogger({ component: 'UpstreamProxy' });

export interface ProxyOptions {
  upstreamUrl: string;
  method: string;
  headers: Headers;
  body?: string | null;
  timeout?: number;
  maxRetries?: number;
  retryDelay?: number;
  retryableStatusCodes?: number[];
}

export interface ProxyResult {
  success: boolean;
  statusCode: number;
  headers: Record<string, string>;
  body: string;
  responseTime: number;
  error?: string;
  retries?: number;
}

/**
 * Circuit breaker state
 */
interface CircuitBreakerState {
  failures: number;
  lastFailureTime: number;
  state: 'closed' | 'open' | 'half-open';
}

const circuitBreakers = new Map<string, CircuitBreakerState>();

const DEFAULT_CIRCUIT_BREAKER_CONFIG = {
  failureThreshold: 5, // Open circuit after 5 failures
  resetTimeout: 60000, // 60 seconds before attempting to close
  halfOpenTimeout: 30000, // 30 seconds in half-open state
};

/**
 * Check if circuit breaker allows request
 */
function checkCircuitBreaker(upstreamUrl: string): boolean {
  const state = circuitBreakers.get(upstreamUrl);
  if (!state) {
    // Initialize as closed
    circuitBreakers.set(upstreamUrl, {
      failures: 0,
      lastFailureTime: 0,
      state: 'closed',
    });
    return true;
  }

  const now = Date.now();
  const config = DEFAULT_CIRCUIT_BREAKER_CONFIG;

  switch (state.state) {
    case 'closed':
      return true;
    
    case 'open':
      // Check if we should transition to half-open
      if (now - state.lastFailureTime > config.resetTimeout) {
        state.state = 'half-open';
        logger.info('Circuit breaker transitioning to half-open', { upstreamUrl });
        return true;
      }
      return false;
    
    case 'half-open':
      // Allow one request to test
      return true;
    
    default:
      return true;
  }
}

/**
 * Record circuit breaker success
 */
function recordSuccess(upstreamUrl: string): void {
  const state = circuitBreakers.get(upstreamUrl);
  if (state) {
    if (state.state === 'half-open') {
      // Success in half-open means we can close the circuit
      state.state = 'closed';
      state.failures = 0;
      logger.info('Circuit breaker closed after successful request', { upstreamUrl });
    } else {
      // Reset failure count on success
      state.failures = 0;
    }
  }
}

/**
 * Record circuit breaker failure
 */
function recordFailure(upstreamUrl: string): void {
  const state = circuitBreakers.get(upstreamUrl);
  if (!state) {
    circuitBreakers.set(upstreamUrl, {
      failures: 1,
      lastFailureTime: Date.now(),
      state: 'closed',
    });
    return;
  }

  state.failures++;
  state.lastFailureTime = Date.now();
  const config = DEFAULT_CIRCUIT_BREAKER_CONFIG;

  if (state.state === 'half-open') {
    // Failure in half-open means we should open again
    state.state = 'open';
    logger.warn('Circuit breaker opened after failure in half-open state', {
      upstreamUrl,
      failures: state.failures,
    });
  } else if (state.failures >= config.failureThreshold) {
    state.state = 'open';
    logger.warn('Circuit breaker opened due to failure threshold', {
      upstreamUrl,
      failures: state.failures,
    });
  }
}

/**
 * Sleep for specified milliseconds
 */
function sleep(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms));
}

/**
 * Calculate exponential backoff delay
 */
function calculateBackoffDelay(attempt: number, baseDelay: number): number {
  return Math.min(baseDelay * Math.pow(2, attempt), 10000); // Max 10 seconds
}

/**
 * Enhanced proxy with retry logic and circuit breaker
 */
export async function proxyRequestWithRetry(options: ProxyOptions): Promise<ProxyResult> {
  const {
    upstreamUrl,
    method,
    headers,
    body,
    timeout = 30000,
    maxRetries = 3,
    retryDelay = 1000,
    retryableStatusCodes = [502, 503, 504, 408, 429],
  } = options;

  // Check circuit breaker
  if (!checkCircuitBreaker(upstreamUrl)) {
    logger.warn('Circuit breaker is open, rejecting request', { upstreamUrl });
    return {
      success: false,
      statusCode: 503,
      headers: {},
      body: JSON.stringify({
        error: 'Service temporarily unavailable',
        code: 'CIRCUIT_BREAKER_OPEN',
        details: 'Upstream service is experiencing issues',
      }),
      responseTime: 0,
      error: 'Circuit breaker is open',
    };
  }

  let lastError: Error | null = null;
  let lastResult: ProxyResult | null = null;

  for (let attempt = 0; attempt <= maxRetries; attempt++) {
    const startTime = Date.now();

    try {
      // Prepare headers (exclude host and connection headers)
      const proxyHeaders: Record<string, string> = {};
      headers.forEach((value, key) => {
        const lowerKey = key.toLowerCase();
        if (
          lowerKey !== 'host' &&
          lowerKey !== 'connection' &&
          lowerKey !== 'x-payment' &&
          lowerKey !== 'x-forwarded-host'
        ) {
          proxyHeaders[key] = value;
        }
      });

      // Add forwarded headers
      proxyHeaders['X-Forwarded-For'] = headers.get('x-forwarded-for') || 'unknown';
      proxyHeaders['X-Forwarded-Proto'] = headers.get('x-forwarded-proto') || 'https';

      // Create fetch request with timeout
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), timeout);

      try {
        const response = await fetch(upstreamUrl, {
          method,
          headers: proxyHeaders,
          body: body || undefined,
          signal: controller.signal,
        });

        clearTimeout(timeoutId);

        // Read response body
        const responseBody = await response.text();

        // Convert response headers to object
        const responseHeaders: Record<string, string> = {};
        response.headers.forEach((value, key) => {
          responseHeaders[key] = value;
        });

        const responseTime = Date.now() - startTime;

        const result: ProxyResult = {
          success: response.ok,
          statusCode: response.status,
          headers: responseHeaders,
          body: responseBody,
          responseTime,
          retries: attempt,
        };

        // Check if we should retry
        if (!response.ok && retryableStatusCodes.includes(response.status) && attempt < maxRetries) {
          const backoffDelay = calculateBackoffDelay(attempt, retryDelay);
          logger.warn('Upstream returned retryable error, retrying', {
            upstreamUrl,
            statusCode: response.status,
            attempt: attempt + 1,
            maxRetries,
            backoffDelay,
          });
          
          await sleep(backoffDelay);
          lastResult = result;
          continue;
        }

        // Success or non-retryable error
        recordSuccess(upstreamUrl);
        return result;
      } catch (fetchError) {
        clearTimeout(timeoutId);
        throw fetchError;
      }
    } catch (error) {
      const responseTime = Date.now() - startTime;
      lastError = error instanceof Error ? error : new Error('Unknown error');

      // Check if error is retryable
      const isRetryable = 
        (error instanceof Error && error.name === 'AbortError') || // Timeout
        (error instanceof TypeError && error.message.includes('fetch')); // Network error

      if (isRetryable && attempt < maxRetries) {
        const backoffDelay = calculateBackoffDelay(attempt, retryDelay);
        logger.warn('Upstream request failed, retrying', {
          upstreamUrl,
          error: lastError.message,
          attempt: attempt + 1,
          maxRetries,
          backoffDelay,
        });
        
        await sleep(backoffDelay);
        continue;
      }

      // Non-retryable error or max retries reached
      recordFailure(upstreamUrl);
      
      if (lastError.name === 'AbortError') {
        return {
          success: false,
          statusCode: 504,
          headers: {},
          body: JSON.stringify({ error: 'Upstream timeout' }),
          responseTime,
          error: 'Request timeout',
          retries: attempt,
        };
      }

      return {
        success: false,
        statusCode: 502,
        headers: {},
        body: JSON.stringify({
          error: 'Upstream error',
          details: lastError.message,
        }),
        responseTime,
        error: lastError.message,
        retries: attempt,
      };
    }
  }

  // Max retries reached
  recordFailure(upstreamUrl);
  
  return {
    success: false,
    statusCode: lastResult?.statusCode || 502,
    headers: lastResult?.headers || {},
    body: lastResult?.body || JSON.stringify({
      error: 'Upstream error after retries',
      details: lastError?.message || 'Unknown error',
    }),
    responseTime: lastResult?.responseTime || 0,
    error: lastError?.message || 'Unknown error',
    retries: maxRetries,
  };
}

/**
 * Get circuit breaker status for monitoring
 */
export function getCircuitBreakerStatus(upstreamUrl?: string): Record<string, any> {
  if (upstreamUrl) {
    const state = circuitBreakers.get(upstreamUrl);
    return {
      [upstreamUrl]: state || {
        failures: 0,
        lastFailureTime: 0,
        state: 'closed',
      },
    };
  }

  // Return all circuit breakers
  const status: Record<string, any> = {};
  circuitBreakers.forEach((state, url) => {
    status[url] = state;
  });
  return status;
}

/**
 * Reset circuit breaker (for testing/admin)
 */
export function resetCircuitBreaker(upstreamUrl: string): void {
  circuitBreakers.delete(upstreamUrl);
  logger.info('Circuit breaker reset', { upstreamUrl });
}






