// =============================================================================
// SESSION MIDDLEWARE
// =============================================================================
// Middleware for checking and validating sessions in requests

import { NextRequest, NextResponse } from 'next/server';
import { getWalletSessionManager } from './wallet-session';
import { getSessionTokenManager } from './session-tokens';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'SessionMiddleware' });

/**
 * Session Check Result
 */
export interface SessionCheckResult {
  hasSession: boolean;
  sessionId?: string;
  walletAddress?: string;
  valid?: boolean;
  error?: string;
  remainingAmount?: string;
}

/**
 * Check for session token in request
 */
export function checkSessionToken(request: NextRequest): SessionCheckResult {
  // Check for session token in headers
  const sessionToken = 
    request.headers.get('x-session-token') || 
    request.headers.get('authorization')?.replace('Bearer ', '');

  if (!sessionToken) {
    return {
      hasSession: false,
    };
  }

  // Validate token
  const tokenManager = getSessionTokenManager();
  const tokenValidation = tokenManager.validateToken(sessionToken);

  if (!tokenValidation.valid || !tokenValidation.payload) {
    return {
      hasSession: true,
      valid: false,
      error: tokenValidation.error || 'Invalid session token',
    };
  }

  const payload = tokenValidation.payload;

  // Validate session exists and is still valid
  const sessionManager = getWalletSessionManager();
  const sessionValidation = sessionManager.validateSession(payload.sessionId);

  if (!sessionValidation.valid || !sessionValidation.session) {
    return {
      hasSession: true,
      sessionId: payload.sessionId,
      walletAddress: payload.walletAddress,
      valid: false,
      error: sessionValidation.error || 'Session invalid',
    };
  }

  return {
    hasSession: true,
    sessionId: payload.sessionId,
    walletAddress: payload.walletAddress,
    valid: true,
    remainingAmount: sessionValidation.remainingAmount,
  };
}

/**
 * Use session for a payment (deduct amount)
 */
export function useSessionForPayment(
  sessionId: string,
  amount: string
): {
  success: boolean;
  remainingAmount?: string;
  error?: string;
} {
  const sessionManager = getWalletSessionManager();
  const result = sessionManager.useSession(sessionId, amount);

  if (!result.valid) {
    return {
      success: false,
      error: result.error,
      remainingAmount: result.remainingAmount,
    };
  }

  return {
    success: true,
    remainingAmount: result.remainingAmount,
  };
}

/**
 * Create session from payment header
 * 
 * This should be called after a payment is verified.
 * Creates a reusable session that can be used for subsequent requests.
 */
export async function createSessionFromPayment(
  paymentHeader: string,
  endpointId: string,
  agentId?: string
): Promise<{
  success: boolean;
  sessionId?: string;
  token?: string;
  expiresAt?: number;
  error?: string;
}> {
  try {
    // Parse and verify payment
    const { parseAndVerifyPaymentHeader } = await import('../payment-header-parser');
    const parsed = await parseAndVerifyPaymentHeader(paymentHeader);

    if (!parsed.valid || !parsed.payment) {
      return {
        success: false,
        error: parsed.error || 'Invalid payment header',
      };
    }

    const payment = parsed.payment;

    // Create session
    const sessionManager = getWalletSessionManager();
    const session = sessionManager.createSession({
      walletAddress: payment.authorization.from,
      authorizedAmount: payment.authorization.value,
      validUntil: parseInt(payment.authorization.validBefore),
      validFrom: parseInt(payment.authorization.validAfter || Math.floor(Date.now() / 1000).toString()),
      network: payment.network,
      asset: payment.authorization.to, // This should be the token address, may need adjustment
      nonce: payment.authorization.nonce,
      signature: payment.signature,
      authorization: payment.authorization,
      endpointId,
      agentId,
      reusable: true,
    });

    // Generate token
    const tokenManager = getSessionTokenManager();
    const tokenResult = tokenManager.generateToken(session);

    logger.info(
      {
        sessionId: session.id,
        walletAddress: session.walletAddress,
        endpointId,
      },
      'Session created from payment'
    );

    return {
      success: true,
      sessionId: session.id,
      token: tokenResult.token,
      expiresAt: tokenResult.expiresAt,
    };
  } catch (error) {
    logger.error({ error }, 'Failed to create session from payment');
    return {
      success: false,
      error: error instanceof Error ? error.message : 'Unknown error',
    };
  }
}

