// =============================================================================
// NEXT.JS ADAPTER FOR X402 SDK
// =============================================================================
// Adapter to use x402 SDK with Next.js App Router

import { NextRequest, NextResponse } from 'next/server';
import { getX402ResourceServer } from './resource-server';
import { createDiscoveryMetadata, getEndpointDiscoveryInfo } from './discovery-metadata';
import type { MeteredEndpoint } from '@/db/metered-endpoints';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'X402NextJSAdapter' });

/**
 * Generate x402-compliant 402 response with Bazaar discovery metadata
 * 
 * This response format enables Bazaar discovery when the facilitator processes payments
 */
export async function create402Response(
  request: NextRequest,
  endpoint: MeteredEndpoint
): Promise<NextResponse> {
  const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 
                 (request.headers.get('x-forwarded-proto') === 'https' ? 'https' : 'http') + 
                 '://' + (request.headers.get('host') || 'localhost:3001');
  const resourceUrl = `${baseUrl}/api/v1/metered/${endpoint.id}`;

  // Get discovery metadata using Bazaar extension (may be stubbed if packages unavailable)
  const discoveryExtension = await createDiscoveryMetadata(endpoint);

  // Ensure price is a string (atomic units)
  const priceString = String(endpoint.price);
  
  // Normalize network to 'base' if not specified (x402scan expects specific network values)
  const network = (endpoint.network || 'base').toLowerCase();
  
  // Build outputSchema in x402scan format: { input: {...}, output?: {...} }
  // x402scan expects outputSchema with input/output structure, not raw JSON schema
  const outputSchema = {
    input: {
      type: 'http' as const,
      method: 'POST' as const,
      bodyType: 'json' as const,
      // Optional: Add queryParams, bodyFields, headerFields if we have more info
    },
    output: {
      type: 'object',
      properties: {
        data: {
          type: 'object',
          description: 'Response data from upstream service',
          additionalProperties: true,
        },
        timestamp: {
          type: 'string',
          format: 'date-time',
          description: 'Response timestamp',
        },
      },
    },
  };

  // Create x402scan-compliant EnhancedPaymentRequirements format
  // x402scan requires strict schema matching - remove extensions, use extra instead
  const accepts: Array<{
    scheme: 'exact';
    network: string;
    maxAmountRequired: string;
    resource: string;
    description: string;
    mimeType: string;
    payTo: string;
    maxTimeoutSeconds: number;
    asset: string;
    outputSchema?: typeof outputSchema;
    extra?: Record<string, any>;
  }> = [];
  
  // Add Base (EVM) accept entry - EnhancedPaymentRequirements format
  // Only include one entry for now to match x402scan validation
  accepts.push({
    scheme: 'exact',
    network: network === 'base' ? 'base' : network, // Ensure 'base' for Base network
    maxAmountRequired: priceString, // Must be string of atomic units
    asset: endpoint.tokenAddress,
    payTo: endpoint.recipientAddress,
    resource: resourceUrl,
    description: endpoint.description || endpoint.name || 'Metered API endpoint',
    mimeType: 'application/json',
    maxTimeoutSeconds: 300,
    outputSchema: outputSchema,
    // Move Bazaar extension to extra field (not in schema, but we can include it)
    extra: {
      bazaar: discoveryExtension.bazaar,
    },
  });

  // x402scan response format - use x402Version: 1 (number type)
  // x402scan validation may only accept version 1
  const response = {
    x402Version: 1, // x402scan expects number, use 1 for compatibility
    error: 'X-PAYMENT header is required',
    accepts: accepts,
  };

  return NextResponse.json(response, { status: 402 });
}

// Note: Payment verification is handled by existing CDP facilitator
// This adapter focuses on Bazaar discovery metadata integration

/**
 * Get route configuration for an endpoint (for Bazaar discovery)
 */
export async function getRouteConfig(endpoint: MeteredEndpoint) {
  const isProduction = process.env.NODE_ENV === 'production';
  const appUrl = process.env.NEXT_PUBLIC_APP_URL;
  if (isProduction && !appUrl) {
    throw new Error('NEXT_PUBLIC_APP_URL must be set in production');
  }
  const baseUrl = appUrl || 'http://localhost:3001';
  const resourceUrl = `${baseUrl}/api/v1/metered/${endpoint.id}`;

  const discoveryExtension = await createDiscoveryMetadata(endpoint);

  return {
    [`POST /api/v1/metered/${endpoint.id}`]: {
      accepts: {
        scheme: 'exact',
        network: endpoint.network,
        price: endpoint.price,
        payTo: endpoint.recipientAddress,
        asset: endpoint.tokenAddress,
        resource: resourceUrl,
        description: endpoint.description || endpoint.name,
        mimeType: 'application/json',
        maxTimeoutSeconds: 300,
      },
      extensions: {
        ...discoveryExtension,
      },
    },
  };
}

