// =============================================================================
// SUBSCRIPTION MANAGER
// =============================================================================
// Manages subscription billing patterns for recurring payments

import { createLogger } from '@/lib/logger';
import { getWalletSessionManager, type WalletSession } from './wallet-session';

const logger = createLogger({ component: 'SubscriptionManager' });

/**
 * Subscription Plan
 */
export interface SubscriptionPlan {
  id: string;
  name: string;
  endpointId: string;
  price: string; // Price per billing period
  billingPeriod: 'hourly' | 'daily' | 'weekly' | 'monthly';
  maxUsage?: number; // Optional: max API calls per period
  network: string;
  asset: string;
}

/**
 * Active Subscription
 */
export interface ActiveSubscription {
  id: string;
  planId: string;
  walletAddress: string;
  endpointId: string;
  status: 'active' | 'paused' | 'cancelled' | 'expired';
  currentPeriodStart: number; // Unix timestamp
  currentPeriodEnd: number; // Unix timestamp
  usageCount: number; // API calls in current period
  sessionId?: string; // Associated wallet session
  createdAt: number;
  updatedAt: number;
}

/**
 * Subscription Manager
 * 
 * Manages subscription billing patterns for recurring payments.
 * Integrates with wallet sessions for payment authorization.
 */
export class SubscriptionManager {
  private subscriptions: Map<string, ActiveSubscription> = new Map();
  private walletSubscriptions: Map<string, Set<string>> = new Map(); // wallet -> subscription IDs
  private plans: Map<string, SubscriptionPlan> = new Map();

  /**
   * Create a subscription plan
   */
  createPlan(plan: SubscriptionPlan): SubscriptionPlan {
    this.plans.set(plan.id, plan);
    logger.info({ planId: plan.id, name: plan.name }, 'Subscription plan created');
    return plan;
  }

  /**
   * Get subscription plan
   */
  getPlan(planId: string): SubscriptionPlan | undefined {
    return this.plans.get(planId);
  }

  /**
   * Create an active subscription
   */
  createSubscription(
    planId: string,
    walletAddress: string,
    sessionId?: string
  ): ActiveSubscription | null {
    const plan = this.getPlan(planId);
    if (!plan) {
      logger.warn({ planId }, 'Subscription plan not found');
      return null;
    }

    const subscriptionId = this.generateSubscriptionId();
    const now = Math.floor(Date.now() / 1000);
    const periodEnd = this.calculatePeriodEnd(now, plan.billingPeriod);

    const subscription: ActiveSubscription = {
      id: subscriptionId,
      planId: plan.id,
      walletAddress: walletAddress.toLowerCase(),
      endpointId: plan.endpointId,
      status: 'active',
      currentPeriodStart: now,
      currentPeriodEnd: periodEnd,
      usageCount: 0,
      sessionId,
      createdAt: now,
      updatedAt: now,
    };

    this.subscriptions.set(subscriptionId, subscription);

    // Index by wallet
    const walletKey = subscription.walletAddress;
    if (!this.walletSubscriptions.has(walletKey)) {
      this.walletSubscriptions.set(walletKey, new Set());
    }
    this.walletSubscriptions.get(walletKey)!.add(subscriptionId);

    logger.info(
      {
        subscriptionId,
        planId,
        walletAddress: subscription.walletAddress,
        periodEnd: subscription.currentPeriodEnd,
      },
      'Subscription created'
    );

    return subscription;
  }

  /**
   * Check if subscription is active and valid
   */
  validateSubscription(subscriptionId: string): {
    valid: boolean;
    subscription?: ActiveSubscription;
    error?: string;
  } {
    const subscription = this.subscriptions.get(subscriptionId);

    if (!subscription) {
      return {
        valid: false,
        error: 'Subscription not found',
      };
    }

    if (subscription.status !== 'active') {
      return {
        valid: false,
        subscription,
        error: `Subscription is ${subscription.status}`,
      };
    }

    const now = Math.floor(Date.now() / 1000);

    // Check if period has expired
    if (now > subscription.currentPeriodEnd) {
      subscription.status = 'expired';
      subscription.updatedAt = now;
      return {
        valid: false,
        subscription,
        error: 'Subscription period expired',
      };
    }

    // Check usage limits
    const plan = this.getPlan(subscription.planId);
    if (plan?.maxUsage && subscription.usageCount >= plan.maxUsage) {
      return {
        valid: false,
        subscription,
        error: 'Subscription usage limit reached',
      };
    }

    return {
      valid: true,
      subscription,
    };
  }

  /**
   * Record subscription usage
   */
  recordUsage(subscriptionId: string): boolean {
    const validation = this.validateSubscription(subscriptionId);
    if (!validation.valid || !validation.subscription) {
      return false;
    }

    const subscription = validation.subscription;
    subscription.usageCount += 1;
    subscription.updatedAt = Math.floor(Date.now() / 1000);

    return true;
  }

  /**
   * Renew subscription period
   */
  renewSubscription(subscriptionId: string, sessionId?: string): boolean {
    const subscription = this.subscriptions.get(subscriptionId);
    if (!subscription) {
      return false;
    }

    const plan = this.getPlan(subscription.planId);
    if (!plan) {
      return false;
    }

    const now = Math.floor(Date.now() / 1000);
    const periodEnd = this.calculatePeriodEnd(now, plan.billingPeriod);

    subscription.currentPeriodStart = now;
    subscription.currentPeriodEnd = periodEnd;
    subscription.usageCount = 0;
    subscription.sessionId = sessionId;
    subscription.updatedAt = now;

    if (subscription.status === 'expired') {
      subscription.status = 'active';
    }

    logger.info(
      {
        subscriptionId,
        newPeriodEnd: periodEnd,
      },
      'Subscription renewed'
    );

    return true;
  }

  /**
   * Cancel subscription
   */
  cancelSubscription(subscriptionId: string): boolean {
    const subscription = this.subscriptions.get(subscriptionId);
    if (!subscription) {
      return false;
    }

    subscription.status = 'cancelled';
    subscription.updatedAt = Math.floor(Date.now() / 1000);

    logger.info({ subscriptionId }, 'Subscription cancelled');

    return true;
  }

  /**
   * Get active subscriptions for a wallet
   */
  getWalletSubscriptions(walletAddress: string): ActiveSubscription[] {
    const walletKey = walletAddress.toLowerCase();
    const subscriptionIds = this.walletSubscriptions.get(walletKey) || new Set();

    const subscriptions: ActiveSubscription[] = [];
    for (const subscriptionId of subscriptionIds) {
      const subscription = this.subscriptions.get(subscriptionId);
      if (subscription && subscription.status === 'active') {
        subscriptions.push(subscription);
      }
    }

    return subscriptions;
  }

  /**
   * Get subscription by ID
   */
  getSubscription(subscriptionId: string): ActiveSubscription | undefined {
    return this.subscriptions.get(subscriptionId);
  }

  /**
   * Calculate period end timestamp
   */
  private calculatePeriodEnd(start: number, period: SubscriptionPlan['billingPeriod']): number {
    const periods: Record<SubscriptionPlan['billingPeriod'], number> = {
      hourly: 3600,
      daily: 86400,
      weekly: 604800,
      monthly: 2592000, // 30 days
    };

    return start + periods[period];
  }

  /**
   * Generate subscription ID
   */
  private generateSubscriptionId(): string {
    return `sub_${Date.now()}_${Math.random().toString(36).substring(2, 11)}`;
  }
}

// Singleton instance
let subscriptionManager: SubscriptionManager | null = null;

/**
 * Get subscription manager instance
 */
export function getSubscriptionManager(): SubscriptionManager {
  if (!subscriptionManager) {
    subscriptionManager = new SubscriptionManager();
  }

  return subscriptionManager;
}

