// =============================================================================
// WALLET SESSION MANAGER
// =============================================================================
// Manages wallet-based sessions for reusable payment authorizations
// Supports subscription/session patterns, not just one-off payments

import { createLogger } from '@/lib/logger';
import type { PaymentAuthorization } from '../signature-verifier';

const logger = createLogger({ component: 'WalletSession' });

/**
 * Wallet Session
 * 
 * Represents a reusable payment authorization that can be used
 * for multiple API calls within a session window.
 */
export interface WalletSession {
  id: string; // Session ID (UUID)
  walletAddress: string; // Wallet address that authorized payment
  authorizedAmount: string; // Total amount authorized (in wei/smallest unit)
  spentAmount: string; // Amount already spent in this session
  validUntil: number; // Unix timestamp when session expires
  validFrom: number; // Unix timestamp when session starts
  network: string; // Network (e.g., 'base', 'ethereum')
  asset: string; // Asset address (e.g., USDC address)
  reusable: boolean; // Whether this session can be reused
  nonce: string; // Original payment nonce
  signature: string; // Original payment signature
  authorization: PaymentAuthorization; // Full authorization object
  endpointId?: string; // Optional: specific endpoint this session is for
  agentId?: string; // Optional: agent using this session
  createdAt: number; // Unix timestamp when session was created
  lastUsedAt: number; // Unix timestamp when session was last used
  usageCount: number; // Number of times this session has been used
}

/**
 * Session Creation Options
 */
export interface SessionOptions {
  walletAddress: string;
  authorizedAmount: string;
  validUntil: number;
  validFrom?: number;
  network: string;
  asset: string;
  nonce: string;
  signature: string;
  authorization: PaymentAuthorization;
  endpointId?: string;
  agentId?: string;
  reusable?: boolean; // Default: true
}

/**
 * Session Validation Result
 */
export interface SessionValidationResult {
  valid: boolean;
  session?: WalletSession;
  error?: string;
  remainingAmount?: string;
}

/**
 * Wallet Session Manager
 * 
 * Manages wallet-based sessions for reusable payment authorizations.
 * Sessions allow agents to make multiple API calls with a single payment authorization.
 */
export class WalletSessionManager {
  private sessions: Map<string, WalletSession> = new Map();
  private walletSessions: Map<string, Set<string>> = new Map(); // wallet -> session IDs

  /**
   * Create a new wallet session from a payment authorization
   */
  createSession(options: SessionOptions): WalletSession {
    const sessionId = this.generateSessionId();
    const now = Math.floor(Date.now() / 1000);

    const session: WalletSession = {
      id: sessionId,
      walletAddress: options.walletAddress.toLowerCase(),
      authorizedAmount: options.authorizedAmount,
      spentAmount: '0',
      validUntil: options.validUntil,
      validFrom: options.validFrom || now,
      network: options.network,
      asset: options.asset,
      reusable: options.reusable !== false, // Default to true
      nonce: options.nonce,
      signature: options.signature,
      authorization: options.authorization,
      endpointId: options.endpointId,
      agentId: options.agentId,
      createdAt: now,
      lastUsedAt: now,
      usageCount: 0,
    };

    // Store session
    this.sessions.set(sessionId, session);

    // Index by wallet address
    const walletKey = session.walletAddress;
    if (!this.walletSessions.has(walletKey)) {
      this.walletSessions.set(walletKey, new Set());
    }
    this.walletSessions.get(walletKey)!.add(sessionId);

    logger.info(
      {
        sessionId,
        walletAddress: session.walletAddress,
        authorizedAmount: session.authorizedAmount,
        validUntil: session.validUntil,
      },
      'Wallet session created'
    );

    return session;
  }

  /**
   * Validate and retrieve a session
   */
  validateSession(sessionId: string): SessionValidationResult {
    const session = this.sessions.get(sessionId);

    if (!session) {
      return {
        valid: false,
        error: 'Session not found',
      };
    }

    // Check if session has expired
    const now = Math.floor(Date.now() / 1000);
    if (now > session.validUntil) {
      this.sessions.delete(sessionId);
      this.walletSessions.get(session.walletAddress)?.delete(sessionId);
      return {
        valid: false,
        error: 'Session expired',
      };
    }

    // Check if session has started
    if (now < session.validFrom) {
      return {
        valid: false,
        error: 'Session not yet valid',
      };
    }

    // Calculate remaining amount
    const authorized = BigInt(session.authorizedAmount);
    const spent = BigInt(session.spentAmount);
    const remaining = authorized > spent ? authorized - spent : BigInt(0);

    return {
      valid: true,
      session,
      remainingAmount: remaining.toString(),
    };
  }

  /**
   * Use a session (deduct amount)
   */
  useSession(sessionId: string, amount: string): SessionValidationResult {
    const validation = this.validateSession(sessionId);

    if (!validation.valid || !validation.session) {
      return validation;
    }

    const session = validation.session;
    const amountToSpend = BigInt(amount);
    const remaining = BigInt(validation.remainingAmount || '0');

    // Check if session has enough remaining balance
    if (amountToSpend > remaining) {
      return {
        valid: false,
        session,
        error: 'Insufficient session balance',
        remainingAmount: remaining.toString(),
      };
    }

    // Update session
    const newSpent = BigInt(session.spentAmount) + amountToSpend;
    session.spentAmount = newSpent.toString();
    session.lastUsedAt = Math.floor(Date.now() / 1000);
    session.usageCount += 1;

    const newRemaining = remaining - amountToSpend;

    logger.info(
      {
        sessionId,
        amount,
        remainingAmount: newRemaining.toString(),
        usageCount: session.usageCount,
      },
      'Session used'
    );

    return {
      valid: true,
      session,
      remainingAmount: newRemaining.toString(),
    };
  }

  /**
   * Get active sessions for a wallet
   */
  getWalletSessions(walletAddress: string): WalletSession[] {
    const walletKey = walletAddress.toLowerCase();
    const sessionIds = this.walletSessions.get(walletKey) || new Set();

    const sessions: WalletSession[] = [];
    for (const sessionId of sessionIds) {
      const session = this.sessions.get(sessionId);
      if (session) {
        const now = Math.floor(Date.now() / 1000);
        if (now <= session.validUntil && now >= session.validFrom) {
          sessions.push(session);
        }
      }
    }

    return sessions;
  }

  /**
   * Get session by ID
   */
  getSession(sessionId: string): WalletSession | undefined {
    return this.sessions.get(sessionId);
  }

  /**
   * Revoke a session
   */
  revokeSession(sessionId: string): boolean {
    const session = this.sessions.get(sessionId);
    if (!session) {
      return false;
    }

    this.sessions.delete(sessionId);
    this.walletSessions.get(session.walletAddress)?.delete(sessionId);

    logger.info({ sessionId }, 'Session revoked');

    return true;
  }

  /**
   * Clean up expired sessions
   */
  cleanupExpiredSessions(): number {
    const now = Math.floor(Date.now() / 1000);
    let cleaned = 0;

    for (const [sessionId, session] of this.sessions.entries()) {
      if (now > session.validUntil) {
        this.sessions.delete(sessionId);
        this.walletSessions.get(session.walletAddress)?.delete(sessionId);
        cleaned++;
      }
    }

    if (cleaned > 0) {
      logger.info({ cleaned }, 'Expired sessions cleaned up');
    }

    return cleaned;
  }

  /**
   * Generate a unique session ID
   */
  private generateSessionId(): string {
    // Generate UUID v4
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c) => {
      const r = (Math.random() * 16) | 0;
      const v = c === 'x' ? r : (r & 0x3) | 0x8;
      return v.toString(16);
    });
  }
}

// Singleton instance
let sessionManager: WalletSessionManager | null = null;

/**
 * Get wallet session manager instance
 */
export function getWalletSessionManager(): WalletSessionManager {
  if (!sessionManager) {
    sessionManager = new WalletSessionManager();
    
    // Clean up expired sessions every 5 minutes
    setInterval(() => {
      sessionManager?.cleanupExpiredSessions();
    }, 5 * 60 * 1000);
  }

  return sessionManager;
}

