// =============================================================================
// Concurrent Request Limiter
// =============================================================================
// Prevents resource exhaustion by limiting concurrent requests per user/IP/endpoint
// Uses in-memory semaphore pattern (can upgrade to Redis for multi-instance)

import { createLogger } from './logger';

const logger = createLogger({ component: 'ConcurrentRequestLimiter' });

export interface ConcurrentLimitConfig {
  maxConcurrent: number; // Maximum concurrent requests
  windowMs?: number; // Time window for tracking (default: 1 minute)
  identifier: string; // Unique identifier (e.g., "ip:1.2.3.4" or "endpoint:ep_123:ip:1.2.3.4")
}

interface RequestTracker {
  count: number;
  lastReset: number;
  activeRequests: Set<string>; // Track active request IDs
}

// In-memory store: { identifier: RequestTracker }
const requestTrackers = new Map<string, RequestTracker>();

// Cleanup old trackers every 5 minutes
setInterval(() => {
  const now = Date.now();
  const maxAge = 5 * 60 * 1000; // 5 minutes
  const keysToDelete: string[] = [];
  
  requestTrackers.forEach((tracker, key) => {
    // Delete if no active requests and last reset was more than maxAge ago
    if (tracker.activeRequests.size === 0 && now - tracker.lastReset > maxAge) {
      keysToDelete.push(key);
    }
  });
  
  keysToDelete.forEach(key => requestTrackers.delete(key));
}, 5 * 60 * 1000);

/**
 * Check if a new request can proceed (not exceeding concurrent limit)
 * Returns true if allowed, false if limit exceeded
 */
export function checkConcurrentLimit(config: ConcurrentLimitConfig): {
  allowed: boolean;
  current: number;
  limit: number;
  identifier: string;
} {
  const windowMs = config.windowMs || 60 * 1000; // Default 1 minute
  const now = Date.now();
  const identifier = config.identifier;
  
  let tracker = requestTrackers.get(identifier);
  
  // Initialize tracker if it doesn't exist
  if (!tracker) {
    tracker = {
      count: 0,
      lastReset: now,
      activeRequests: new Set(),
    };
    requestTrackers.set(identifier, tracker);
  }
  
  // Reset count if window expired
  if (now - tracker.lastReset > windowMs) {
    tracker.count = 0;
    tracker.lastReset = now;
    tracker.activeRequests.clear();
  }
  
  // Check if we're at the limit
  const currentActive = tracker.activeRequests.size;
  const allowed = currentActive < config.maxConcurrent;
  
  return {
    allowed,
    current: currentActive,
    limit: config.maxConcurrent,
    identifier,
  };
}

/**
 * Register a request as starting (increment counter)
 * Returns a release function that must be called when request completes
 */
export function acquireConcurrentSlot(config: ConcurrentLimitConfig, requestId: string): {
  success: boolean;
  release: () => void;
  current: number;
  limit: number;
} {
  const check = checkConcurrentLimit(config);
  
  if (!check.allowed) {
    logger.warn('Concurrent request limit exceeded', {
      identifier: config.identifier,
      current: check.current,
      limit: check.limit,
      requestId,
    });
    
    return {
      success: false,
      release: () => {}, // No-op release function
      current: check.current,
      limit: check.limit,
    };
  }
  
  // Acquire slot
  const tracker = requestTrackers.get(config.identifier)!;
  tracker.activeRequests.add(requestId);
  tracker.count++;
  
  logger.debug('Concurrent request slot acquired', {
    identifier: config.identifier,
    current: tracker.activeRequests.size,
    limit: config.maxConcurrent,
    requestId,
  });
  
  // Return release function
  return {
    success: true,
    release: () => {
      const currentTracker = requestTrackers.get(config.identifier);
      if (currentTracker) {
        currentTracker.activeRequests.delete(requestId);
        // Don't decrement count - let it reset with the window
      }
    },
    current: tracker.activeRequests.size,
    limit: config.maxConcurrent,
  };
}

/**
 * Get concurrent request limits for different scopes
 */
export function getConcurrentLimits(): {
  global: number; // Global per-IP limit
  endpoint: number; // Per-endpoint per-IP limit
  user: number; // Per-user (API key) limit
} {
  return {
    global: parseInt(process.env.MAX_CONCURRENT_REQUESTS_GLOBAL || '50', 10),
    endpoint: parseInt(process.env.MAX_CONCURRENT_REQUESTS_ENDPOINT || '20', 10),
    user: parseInt(process.env.MAX_CONCURRENT_REQUESTS_USER || '30', 10),
  };
}

/**
 * Create identifier for global IP-based limit
 */
export function createGlobalIdentifier(ip: string): string {
  return `global:ip:${ip}`;
}

/**
 * Create identifier for endpoint-based limit
 */
export function createEndpointIdentifier(endpointId: string, ip: string): string {
  return `endpoint:${endpointId}:ip:${ip}`;
}

/**
 * Create identifier for user-based limit (API key)
 */
export function createUserIdentifier(apiKeyHash: string): string {
  return `user:${apiKeyHash}`;
}

