// =============================================================================
// CHAIN DETECTION AND VALIDATION
// =============================================================================
// Utilities for detecting and validating blockchain networks

import { getChainRegistry } from './chain-registry';
import {
  parseCAIPNetwork,
  parseCAIPAsset,
  networkToCAIP,
  validateCAIPNetwork,
  validateCAIPAsset,
  isEVMNetwork,
  type CAIPNetworkId,
  type CAIPAssetId,
} from './caip-utils';

/**
 * Chain detection result
 */
export interface ChainDetectionResult {
  detected: boolean;
  network?: CAIPNetworkId;
  legacyName?: string;
  chainId?: number;
  isEVM?: boolean;
  isSolana?: boolean;
  error?: string;
}

/**
 * Detect chain from various input formats
 */
export function detectChain(input: string | number): ChainDetectionResult {
  const chainRegistry = getChainRegistry();

  // Try as CAIP identifier first
  if (typeof input === 'string' && input.includes(':')) {
    if (validateCAIPNetwork(input)) {
      const parsed = parseCAIPNetwork(input);
      const network = chainRegistry.get(input);
      return {
        detected: true,
        network: input,
        legacyName: network?.name,
        chainId: parsed?.chainId || undefined,
        isEVM: parsed?.namespace === 'eip155',
        isSolana: parsed?.namespace === 'solana',
      };
    }
  }

  // Try as legacy network name
  if (typeof input === 'string') {
    const caip = networkToCAIP(input);
    if (caip) {
      const parsed = parseCAIPNetwork(caip);
      const network = chainRegistry.get(caip);
      return {
        detected: true,
        network: caip,
        legacyName: input.toLowerCase(),
        chainId: parsed?.chainId || undefined,
        isEVM: parsed?.namespace === 'eip155',
        isSolana: parsed?.namespace === 'solana',
      };
    }
  }

  // Try as chain ID (EVM only)
  if (typeof input === 'number') {
    const network = chainRegistry.getAll().find((n) => n.chainId === input);
    if (network) {
      return {
        detected: true,
        network: network.caip,
        legacyName: network.name,
        chainId: input,
        isEVM: network.isEVM,
        isSolana: network.isSolana,
      };
    }
  }

  return {
    detected: false,
    error: `Could not detect chain from input: ${input}`,
  };
}

/**
 * Validate chain is supported
 */
export function validateChain(caip: CAIPNetworkId): {
  valid: boolean;
  supported: boolean;
  network?: ReturnType<typeof getChainRegistry>['get'];
  error?: string;
} {
  if (!validateCAIPNetwork(caip)) {
    return {
      valid: false,
      supported: false,
      error: 'Invalid CAIP-2 network identifier format',
    };
  }

  const chainRegistry = getChainRegistry();
  const network = chainRegistry.get(caip);

  if (!network) {
    return {
      valid: true,
      supported: false,
      error: 'Network not supported',
    };
  }

  return {
    valid: true,
    supported: true,
    network,
  };
}

/**
 * Validate asset is supported on a network
 */
export function validateAssetOnNetwork(
  asset: CAIPAssetId,
  network: CAIPNetworkId
): {
  valid: boolean;
  supported: boolean;
  error?: string;
} {
  if (!validateCAIPAsset(asset)) {
    return {
      valid: false,
      supported: false,
      error: 'Invalid CAIP-19 asset identifier format',
    };
  }

  const parsed = parseCAIPAsset(asset);
  if (!parsed) {
    return {
      valid: false,
      supported: false,
      error: 'Could not parse CAIP-19 asset identifier',
    };
  }

  // Check if asset network matches requested network
  // parsed.network is a ParsedCAIPNetwork object, need to reconstruct CAIP
  const assetNetworkCAIP = `${parsed.network.namespace}:${parsed.network.reference}`;
  if (assetNetworkCAIP !== network) {
    return {
      valid: true,
      supported: false,
      error: `Asset network ${assetNetworkCAIP} does not match requested network ${network}`,
    };
  }

  return {
    valid: true,
    supported: true,
  };
}

/**
 * Get compatible networks for an asset
 */
export function getCompatibleNetworks(asset: CAIPAssetId): CAIPNetworkId[] {
  const parsed = parseCAIPAsset(asset);
  if (!parsed) {
    return [];
  }

  const chainRegistry = getChainRegistry();
  const compatible: CAIPNetworkId[] = [];

  // For ERC20 tokens, find all EVM networks that might support this token
  if (parsed.assetNamespace === 'erc20') {
    const evmNetworks = chainRegistry.getEVMs();
    for (const network of evmNetworks) {
      // In a real implementation, you'd check if the token contract exists on that network
      // For now, we'll return all EVM networks as potentially compatible
      compatible.push(network.caip);
    }
  }

  return compatible;
}

/**
 * Normalize chain identifier to CAIP format
 */
export function normalizeToCAIP(input: string | number): CAIPNetworkId | null {
  const detection = detectChain(input);
  return detection.detected ? detection.network || null : null;
}

/**
 * Check if two networks are compatible (same chain family)
 */
export function areNetworksCompatible(
  network1: CAIPNetworkId,
  network2: CAIPNetworkId
): boolean {
  const parsed1 = parseCAIPNetwork(network1);
  const parsed2 = parseCAIPNetwork(network2);

  if (!parsed1 || !parsed2) {
    return false;
  }

  // Same namespace means compatible (e.g., both EVM, both Solana)
  return parsed1.namespace === parsed2.namespace;
}

