// =============================================================================
// SESSION TOKEN GENERATION
// =============================================================================
// JWT-based session tokens for wallet sessions

import { createLogger } from '@/lib/logger';
import type { WalletSession } from './wallet-session';

const logger = createLogger({ component: 'SessionTokens' });

/**
 * Session Token Payload
 */
export interface SessionTokenPayload {
  sessionId: string;
  walletAddress: string;
  endpointId?: string;
  agentId?: string;
  iat: number; // Issued at
  exp: number; // Expiration
}

/**
 * Session Token Result
 */
export interface SessionTokenResult {
  token: string;
  expiresAt: number;
  sessionId: string;
}

/**
 * Session Token Manager
 * 
 * Generates and validates JWT tokens for wallet sessions.
 * Uses a simple secret-based signing (can be upgraded to asymmetric keys).
 */
export class SessionTokenManager {
  private secret: string;

  constructor(secret?: string) {
    // Use environment variable or generate a default (not secure for production!)
    this.secret = secret || process.env.SESSION_TOKEN_SECRET || 'change-me-in-production';
    
    if (this.secret === 'change-me-in-production') {
      logger.warn('Using default session token secret - set SESSION_TOKEN_SECRET in production!');
    }
  }

  /**
   * Generate a session token from a wallet session
   */
  generateToken(session: WalletSession): SessionTokenResult {
    const now = Math.floor(Date.now() / 1000);
    
    // Token expires when session expires (or 24 hours, whichever is sooner)
    const maxTokenExpiry = now + (24 * 60 * 60); // 24 hours
    const tokenExpiry = Math.min(session.validUntil, maxTokenExpiry);

    const payload: SessionTokenPayload = {
      sessionId: session.id,
      walletAddress: session.walletAddress,
      endpointId: session.endpointId,
      agentId: session.agentId,
      iat: now,
      exp: tokenExpiry,
    };

    // Simple base64 encoding for now (can upgrade to proper JWT library)
    // In production, use a proper JWT library like 'jsonwebtoken'
    const token = this.encodeToken(payload);

    logger.debug(
      {
        sessionId: session.id,
        walletAddress: session.walletAddress,
        expiresAt: tokenExpiry,
      },
      'Session token generated'
    );

    return {
      token,
      expiresAt: tokenExpiry,
      sessionId: session.id,
    };
  }

  /**
   * Validate and decode a session token
   */
  validateToken(token: string): {
    valid: boolean;
    payload?: SessionTokenPayload;
    error?: string;
  } {
    try {
      const payload = this.decodeToken(token);

      if (!payload) {
        return {
          valid: false,
          error: 'Invalid token format',
        };
      }

      // Check expiration
      const now = Math.floor(Date.now() / 1000);
      if (payload.exp < now) {
        return {
          valid: false,
          error: 'Token expired',
        };
      }

      return {
        valid: true,
        payload,
      };
    } catch (error) {
      return {
        valid: false,
        error: error instanceof Error ? error.message : 'Token validation failed',
      };
    }
  }

  /**
   * Encode token payload (simple base64 + HMAC for now)
   * TODO: Upgrade to proper JWT library (jsonwebtoken) in production
   */
  private encodeToken(payload: SessionTokenPayload): string {
    // Simple encoding: base64(payload) + '.' + HMAC signature
    const payloadJson = JSON.stringify(payload);
    const payloadB64 = Buffer.from(payloadJson).toString('base64url');
    
    // Create HMAC signature
    const crypto = require('crypto');
    const hmac = crypto.createHmac('sha256', this.secret);
    hmac.update(payloadB64);
    const signature = hmac.digest('base64url');

    return `${payloadB64}.${signature}`;
  }

  /**
   * Decode and verify token
   */
  private decodeToken(token: string): SessionTokenPayload | null {
    try {
      const parts = token.split('.');
      if (parts.length !== 2) {
        return null;
      }

      const [payloadB64, signature] = parts;

      // Verify signature
      const crypto = require('crypto');
      const hmac = crypto.createHmac('sha256', this.secret);
      hmac.update(payloadB64);
      const expectedSignature = hmac.digest('base64url');

      if (signature !== expectedSignature) {
        logger.warn('Token signature mismatch');
        return null;
      }

      // Decode payload
      const payloadJson = Buffer.from(payloadB64, 'base64url').toString('utf-8');
      const payload = JSON.parse(payloadJson) as SessionTokenPayload;

      return payload;
    } catch (error) {
      logger.error({ error }, 'Failed to decode token');
      return null;
    }
  }
}

// Singleton instance
let tokenManager: SessionTokenManager | null = null;

/**
 * Get session token manager instance
 */
export function getSessionTokenManager(): SessionTokenManager {
  if (!tokenManager) {
    tokenManager = new SessionTokenManager();
  }

  return tokenManager;
}

